.center {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

* {
    font-family: 'Raleway', sans-serif;
}

h2 {
    font-size: 2rem;
    margin-bottom: 1rem;
}

p {
    font-size: 1.2rem;
    margin-bottom: 1rem;
}

input[type="text"] {
    padding: 0.5rem;
    font-size: 1.2rem;
    border: 1px solid gray;
    border-radius: 5px;
    margin-right: 1rem;
}

button {
    padding: 0.5rem 1rem;
    font-size: 1.2rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: blue;
    color: white;
    margin-top: 1rem;
}

button:hover {
    background-color: blue;
}

@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;500&display=swap');

.center button {
    margin-left: 0.5rem;
}